import { yupResolver } from '@hookform/resolvers/yup';
import { Button, makeStyles } from '@material-ui/core';
import InputField from 'components/form-controls/InputField/Index';
import PasswordField from 'components/form-controls/PasswordField';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import PropTypes from 'prop-types';
import * as yup from 'yup';
Details.propTypes = {
  userInfor: PropTypes.object,
  onSubmit: PropTypes.func,
};
const useStyles = makeStyles((theme) => ({
  root: {
    justifyContent: 'flex-end',
    display: 'flex',
  },
}));
function Details({ userInfor = {}, onSubmit = null }) {
  const loadingUser = useSelector((state) => state.user.loading);
  const classes = useStyles();
  const schema = yup.object().shape({
    name: yup.string().trim().required('Hy nhp h v tn ca bn'),
    email: yup.string().required('Nhp email ca bn').email('Hy nhp ng nh dng email').trim(),
    // email: yup.string().required('Nhp h v tn ca bn.'),
    password: yup.string().trim(),
    phone: yup
      .string()
      .typeError('Xin hy nhp s')
      .matches('(09|01[2|6|8|9])+([0-9]{8})', 'Xin hy nhp ng s in thoi ')
      .trim(),
  });
  const form = useForm({
    defaultValues: {
      // c th s dng gi tr "" hoc null thay th ty
      name: userInfor.name,
      email: userInfor.email,
      password: userInfor.retypePassword,
      phone: userInfor.phone,
    },
    resolver: yupResolver(schema),
  });
  const handleSubmit = (values) => {
    if (onSubmit) {
      onSubmit(values);
    }
    form.reset();
  };
  return (
    <div>
      <h2>Thng tin ti khon</h2>
      <form onSubmit={form.handleSubmit(handleSubmit)}>
        <InputField form={form} name="name" label="H v tn" />
        <InputField form={form} name="email" label="Email" disabled />
        <PasswordField form={form} name="password" label="Password" />
        <InputField form={form} name="phone" label="S in thoi" />
        <Button
          type="submit"
          variant="contained"
          fullWidth
          className={classes.submit}
          size="large"
          disabled={loadingUser}
          color="secondary"
        >
          Cp nht thng tin
        </Button>
      </form>
    </div>
  );
}

export default Details;
